html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
.list {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
  display: inline-block;
}

body {
  color: $text-color;
  font-family: $main-font;
  font-size: $main-fontsize;
  line-height: 1.33;
}

button {
  cursor: pointer;
}
